//layout mixins - start

@mixin mkdfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin mkdfTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin mkdfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin mkdfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin mkdfTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

//layout mixins - end

//transition mixins - start

@mixin mkdfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin mkdfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin mkdfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin mkdfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin mkdfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

//transition mixins - end

//checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;
%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;
    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }
    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }
    input[type=checkbox] + label span.mkdf-label-text {
        padding-left: 10px;
        display: inline-block;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }
    input[type=checkbox] + label .mkdf-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid $default-border-color;
        background-color: $default-background-color;
        border-radius: 2px;
        &:hover {
            cursor: pointer;
        }
    }
    input[type=checkbox] + label .mkdf-label-view:after {
        content: '';
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        background-color: $first-main-color;
        opacity: 0;
        @include mkdfTransition(opacity 0.3s cubic-bezier(0.02, 0.54, 0.58, 1));
    }
    input[type=checkbox]:checked + label .mkdf-label-view:after {
        opacity: 1;
    }
}

//checkbox mixins - end

/* common mixins - start */

@mixin mkdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true) {

    @if ($with-hover) {

        &:hover {

            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include mkdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            @include mkdfTransition(opacity .2s cubic-bezier(0.02, 0.54, 0.58, 1));
			border-radius: 8px;
        }

    } @else {
        @include mkdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        @include mkdfTransition(opacity .2s cubic-bezier(0.02, 0.54, 0.58, 1));
		border-radius: 8px;
    }
}

@mixin mkdfStandardPaginationStyle($list_type: null) {
	@include mkdfRelativeHolderLayout(top);
	margin: 40px 0 0;
	clear: both;
	font-family: $default-heading-font;
	font-weight: 400;
	font-size: 11px;
	
	ul {
		@include mkdfRelativeHolderLayout(top);
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 8px;
            line-height: 40px;
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0;
                color: #938da6;
                width:40px;
                height: 40px;
			}

            a:hover{
              color: $first-main-color;
            }
			
			&.mkdf-pag-active {
				
				a {
                  color: #fff;
                  background-color: $first-main-color;
                  border-radius: 50%;
				}
			}
			
			&.mkdf-pag-prev,
			&.mkdf-pag-next,
			&.mkdf-pag-first,
			&.mkdf-pag-last {
				margin: 0 2px;
				
				a {
					font-size: 24px;
					
					span {
						display: block;
						line-height: inherit;
						
						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}
			
			@if ($list_type == 'shortcode') {
				
				&.mkdf-pag-prev {
					
					a {
						opacity: 0;
					}
				}
				
				&.mkdf-pag-next {
					
					a {
						opacity: 1;
					}
				}
				
			} @else if ($list_type == 'shop') {
				
				span {
					position: relative;
					display: inline-block;
					vertical-align: top;
					margin: 0;
					padding: 0;
					color: #fff;
                    background-color: $first-main-color;
                    border-radius: 50%;
                    width:40px;
                    height: 40px;
				}
				
				a {
					
					&.next,
					&.prev {
						font-size: 0;
						line-height: 0;
						
						&:before {
							display: block;
							font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
							font-size: 24px;
							line-height: 40px;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
					}
					
					&.prev {
						margin-right: -10px;
						
						&:before {
							content: "\34";
						}
					}
					
					&.next {
						margin-left: -10px;
						
						&:before {
							content: "\35";
						}
					}
				}
			}
		}
	}
}

@mixin mkdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $default-heading-font;
    font-size: 11px;
    line-height: 2em;
    letter-spacing: 0.125em;
	border-radius: 3em;
    font-weight: 500;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;
    @include mkdfTransition(color .2s cubic-bezier(0.02, 0.54, 0.58, 1), background-color .2s cubic-bezier(0.02, 0.54, 0.58, 1), border-color .2s cubic-bezier(0.02, 0.54, 0.58, 1));
}

@mixin mkdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
		padding: 6px 18px;
        
    } @else if ($size == 'medium') {
		padding: 8px 28px;
        
    } @else if ($size == 'large') {
        padding: 12px 37px;
		font-size: 12px;
        
    } @else if ($size == 'huge') {
        display: block;
        text-align: center;
        padding: 13px 26px;
		font-size: 12px;
    }
}

@mixin mkdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin mkdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border: 1px solid transparent $important;
}

@mixin mkdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin mkdfButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin mkdfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin mkdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }
    &:-moz-placeholder {
        @content;
        opacity: 1;
    }
    &::-moz-placeholder {
        @content;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

/* common mixins - end */

/* mixins styles - start */

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 20px;
    padding: 14px 30px;
    font-family: $default-heading-font;
    font-size: 11px;
    line-height: 20px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: .125em;
    color: #807d78;
    background-color: #f5f9ff;
    border: 0;
    border-radius: 8px;
    outline: 0;
    -webkit-appearance: none;
    cursor: pointer;
    box-sizing: border-box;
    @include mkdfTransition(border-color 0.2s cubic-bezier(0.02, 0.54, 0.58, 1));

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include mkdfPlaceholder {
        color: inherit;
    }
}

